@import url("./../tailwind.css");
@import url("./../theme.css");

header {
    background-color: var(--header-background-color, #1f1f1f);
    padding: var(--header-padding, 1rem);
}
nav ul {
    list-style: var(--nav-ul-list-style, none);
    display: var(--nav-ul-display, flex);
    justify-content: var(--nav-ul-justify-content, space-around);
    padding: var(--nav-ul-padding, 0);
}

nav ul li {
    margin: var(--nav-ul-li-margin, 0 1rem);
}


nav ul li a {
    color: var(--nav-ul-li-a-color, #ffffff);
    text-decoration: var(--nav-ul-li-a-text-decoration, none);
    font-weight: var(--nav-ul-li-a-font-weight, bold);
}

@media (max-width: 768px) {
    nav ul {
        flex-direction: var(--nav-ul-flex-direction-mobile, column);
    }
}